<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="120"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">Documentos en línea</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"></v-col>
      </v-row>

      <v-row class="text-center">
        <v-col v-for="(item, i) in items" :key="i" cols="4">
          <template v-if="item.enabled">
            <div @click="goTo(item)">
              <div style="background-color: #EDEDED; border-radius: 1rem; padding: 0.5rem">
                <v-img
                    :src="item.iconUrl"
                    class="my-3"
                    contain
                    height="48"
                />
              </div>
              <div class="mt-2">{{ item.name }}</div>
            </div>
          </template>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex"
import axios from "axios"

export default {
  name: 'DocumentsIndex',
  data: () => ({
    items: [
      //{title: 'Mi abogado LegalHelp', icon: 'home-01.png', link: '/my-lawyer', visible: true},
    ]
  }),
  computed: {
    ...mapGetters(['user', 'services']),
    service() {
      let type = this.$route.params.type
      return this.services.find(service => service.type === type)
    }
  },
  mounted() {
    this.loadDocuments()
  },
  methods: {
    goToChat() {
      this.$router.push({name: 'MyLawyerPaymentChat'})
    },
    goTo(item) {
      this.$router.push({name: 'DocumentsLevel1', params: {id: item.id}})
    },
    loadDocuments() {
      let url = 'categories?type=document'
      axios.get(url).then((response) => {
        this.items = response.data
      })
    }
  }
}
</script>
